@import "./../../_configcss";

.tasks {
	&__list {
		background-color: $list-bg-color;
		border-radius: 10px;
		flex: 1 1 22%;
		padding: 12px 12px 8px;
		overflow: auto;
		max-height: 100%;
		display: flex;
		flex-direction: column;
	}

	&__title {
		font-size: $text-regular-size;
		font-weight: 700;
		margin: 0 0 15px;
		background-color: $color-light-gray;
		padding: 7px 9px 7px 8px;
		border-radius: 5px;
	}

	&__wrap {
		overflow: auto;
		padding: 0 3px 5px 0;

		&::-webkit-scrollbar-track {
			border: 1px solid $color-light-gray;
		}
	}

	&__item {
		padding: 7px 9px 7px 8px;
		background-color: $item-bg-color;
		border-radius: 5px;
		margin-bottom: 15px;

		&:nth-last-child(1) {
			background-color: 17px;
		}
	}

	&__input {
		display: block;
		width: 100%;
		box-sizing: border-box;
		border: 0px;
		border-radius: 5px;
		padding: 7px 9px 7px 8px;
		font-size: 18px;
		font-weight: 400;
		line-height: 21px;
		margin-bottom: 15px;
	}

	&__select {
		display: block;
		width: 100%;
		box-sizing: border-box;
		border: 0px;
		border-radius: 5px;
		padding: 7px 9px 7px 8px;
		font-size: 16px;
		font-weight: 400;
		line-height: 21px;
		margin-bottom: 15px;

		& option {
			border: 0px;
			border-radius: 5px;
		}
	}

	&__add {
		color: $text-add-btn;
		background-color: transparent;

		&:hover {
			background-color: $add-btn-bg-color;
			box-shadow: 4px 4px 16px rgba(0, 0, 0, 0.1);
			color: $text-add-btn;
		}

		&-active {
			background-color: $add-btn-bg-color;
			color: $text-add-btn;
		}

		&[disabled] {
			pointer-events: none;
		}

	}

	&__plus {
		font-size: 19px;
	}


}