@import "./../../_configcss";

.task{	
	&__window{
		box-sizing: border-box;
		position: relative;
		background-color: $item-bg-color;		
		height: 100%;
		width: 100%;
		border-radius: 5px;
		padding: 22px 28px;
	}
	&__title {
		font-size: $text-subtitle-size;
		line-height: 28px;
		font-weight: 500;
		margin-bottom: 14px;
		display: inline-block;
	}
	&__status{
		// background-color: $list-bg-color;
		// padding: 6px 8px 5px;
		// margin-bottom: 5px;
		// display: inline-block;
		// font-size: 14px;
		// border-radius: 5px;
		display: inline-block;
		border-radius: 50%;
		background-color: $list-bg-color;
		height: 30px;
		width: 30px;
		margin-right: 10px;
		vertical-align: text-bottom;
		text-align: center;
		cursor: help;
		&-icon{
			vertical-align: baseline-middle;
			vertical-align: -webkit-baseline-middle;
			width: 12px;
		}
	}
	&__info {
		overflow: auto;
		max-width: 621px;
		height: calc(100vh - 325px);
		margin-bottom: 20px;
		margin-top: 0;
		padding: 15px;
		box-shadow: 4px 4px 16px rgba(0, 0, 0, 0.1);
		border-radius: 15px;	
		white-space: pre-line;
	}
	&__close {
		height: 23px;
		width: 23px;	
		position: absolute;	
		right: 23px;
		top:17px;	
		cursor: pointer;
		& img:hover{
			transform: scale(1.3);
		}
	}
}
textarea{
	&.task {
		&__info {
			width: 100%;
			box-sizing: border-box;
			display: block;
			border-radius: 5px;
			padding: 15px;
			font-family: $font-family;
			font-size: $text-regular-size;
			font-weight: $text-regular-weight;
			line-height: 21px;
		}
	}


}


